<template lang="pug">
#work
  .content-container(
    :style="{ opacity: showEl ? 1 : 0, transform: showEl ? 'translateY(0px)' : 'translateY(-20px)' }"
  )
    .heading 
      .heading-row
        p Work #[br] Experience
        .heading-row-line
      .heading-column
        .heading-column-line
        .inner-content
          .date-container
            .date(
              v-for="(date, index) in years",
              :key="'date-' + index",
              @click="selectDate(date)",
              :class="{ 'selected-year': date === selectedYear }"
            )
              p {{ date }}
              svg.bi.bi-caret-right-fill(
                v-if="date === selectedYear",
                width="16px",
                height="16px",
                viewBox="0 0 16 16",
                fill="#30DEFF",
                xmlns="http://www.w3.org/2000/svg"
              )
                path(
                  d="M12.14 8.753l-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z"
                )
          //- .vertical-divider
          .description-container
            #desc.description
              .company-duration
                p {{ selectedTimeline.date }}
              .company-role
                p {{ selectedTimeline.role }} @ #[span.highlight-text {{ selectedTimeline.company }}]
              .company-description
                svg.bi.bi-caret-right-fill(
                  width="16px",
                  height="16px",
                  viewBox="0 0 16 16",
                  fill="currentColor",
                  xmlns="http://www.w3.org/2000/svg"
                )
                  path(
                    d="M12.14 8.753l-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z"
                  )
                p {{ selectedTimeline.companyDescription }}
              .responsibilities
                p.header Responsibilities:
                .resp-item(
                  v-for="(resp, index) in selectedTimeline.workDescription",
                  :key="'resp-' + index"
                )
                  svg.bi.bi-caret-right-fill(
                    width="16px",
                    height="16px",
                    viewBox="0 0 16 16",
                    fill="currentColor",
                    xmlns="http://www.w3.org/2000/svg"
                  )
                    path(
                      d="M12.14 8.753l-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z"
                    )
                  p {{ resp }}
</template>

<script>
export default {
  data() {
    return {
      showEl: false,
      selectedYear: 2020,
      companies: ["Compass Interactive", "Configura"],
      years: [2020, 2019, 2018],
      timelines: [
        {
          year: 2020,
          date: "February 2020 - Present",
          role: "Full Stack Developer",
          company: "Compass Interactive",
          companyRole: "Compass Interactive - Full Stack Developer",
          companyDescription: "Award winning digital agency",
          workDescription: [
            "Developed and maintained e-commerce/microsites for MNCs like Nike, IKEA, Kiehls, Brickmagic",
            "Involved in planning and estimation for project requirements",
            "Managed multiple projects at a time",
          ],
        },
        {
          year: 2019,
          date: "February 2019 - September 2019",
          role: "Software Developer",
          company: "Configura",
          companyRole: "Configura - Software Developer",
          companyDescription:
            "Swedish-based multinational company specialising in space-planning software with clients from the top companies in the industry globally.",
          workDescription: [
            "Worked in cross-regional teams to automate the build, test, release, and versioning process of the company's proprietary software.",
            "Optimised the total build time from hours to minutes",
            "Managed and supported the CI/CD ecosystem which included JIRA integration, automated ticket updates, automated release branch merges, and more.",
          ],
        },
        {
          year: 2018,
          date: "January 2018 - July 2018",
          role: "Intern",
          company: "Configura",
          companyRole: "Configura - Intern",
          companyDescription:
            "Swedish-based multinational company specialising in space-planning software with clients from the top companies in the industry globally.",
          workDescription: [
            "Implemented a high accuracy room detection system of floor plan images taken by low quality cameras",
            "Improved the quality and speed of the photo rendering system within the company's software.",
            "Added a preview feature which sped up the rendering from minutes to seconds.",
            "Added various other improvements and functionalities to the company's software.",
          ],
        },
      ],
    };
  },
  methods: {
    selectDate(year) {
      if (year === this.selectedYear) return;
      const el = document.getElementById("desc");
      el.style.opacity = 0;
      el.style.transform = "translateX(-10px)";
      setTimeout(() => {
        this.selectedYear = year;
        el.style.opacity = 1;
        el.style.transform = "translateX(0px)";
      }, 500);
    },
    initIntersectionObserver() {
      // eslint-disable-next-line
      const observer = new IntersectionObserver(
        (entries) => {
          if (entries[0].intersectionRatio > 0) {
            if (!this.showEl) {
              this.showEl = true;
            }
            // console.log("Cross Work");
          }
        },
        {
          // rootMargin: "0px 0px -200px 0px",
          threshold: 0.5,
        }
      );
      observer.observe(document.querySelector("#work"));
      this.$store.state.intersectionObserver.observe(
        document.querySelector("#work")
      );
    },
  },
  computed: {
    timelineDates() {
      return this.timelines.map((x) => x.date);
    },
    selectedTimeline() {
      return this.timelines.find((x) => x.year === this.selectedYear);
    },
  },
  mounted() {
    // eslint-disable-next-line
    setTimeout(() => {
      this.initIntersectionObserver();
    }, 2000);
  },
};
</script>

<style lang="sass" scoped>
#work
  width: 100%
  position: relative
  padding: 100px 0px

.content-container
  position: relative
  width: 100%
  max-width: 1024px
  margin: auto
  transition: all 1s ease

.heading
  text-align: left
  display: flex
  align-items: flex-end
  flex-direction: column
  .heading-row
    display: flex
    align-items: center
    flex-direction: row-reverse
    p
      color: #30DEFF
      font-size: 42px
      font-weight: bold
    .heading-row-line
      width: 400px
      height: 1px
      flex-grow: 1
      background-color: #30DEFF
      margin-right: 20px
  .heading-column
    display: flex
    align-items: flex-start
    width: 100%
    flex-direction: row-reverse
    flex-grow: 1
    .heading-column-line
      width: 1px
      background-color: #30DEFF
      margin-top: 10px
      margin-right: 20px
      height: 400px

.inner-content
  padding: 20px 20px 0px 100px
  width: 100%
  display: flex

  .date-container

    .date
      padding: 10px 20px
      cursor: pointer
      display: flex
      align-items: center
      border-right: 1px solid #30DEFF
      p
        font-size: 20px
        padding-right: 15px
    .date:hover
      background-color: rgba(255, 255, 255, 0.2)
    .selected-year
      p
        color: #30DEFF
  .description-container
    .description
      opacity: 1
      transition: opacity 0.5s ease, transform 0.5s ease
      flex-grow: 0
      padding: 10px 40px
      text-align: left

    .company-description
      display: flex
      align-items: flex-start
      text-align: left
      svg
        flex-shrink: 0
        padding-right: 10px
        padding-top: 3px

    .company-role
      padding-bottom: 10px
      text-align: left
      p
        font-size: 20px

    .company-duration
      p
        font-style: italic

    .responsibilities
      margin-top: 40px

    .resp-item
      margin-top: 10px
      display: flex
      align-items: flex-start
      text-align: left
      svg
        flex-shrink: 0
        padding-right: 10px
        padding-top: 3px

.highlight-text
  color: #30DEFF
  font-weight: bold

.vertical-divider
  width: 1px
  height: 400px
  background-color: #30DEFF
</style>
